* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  max-width: 1440px;
  max-height: 960px;
  margin: 0 auto;
  height: 100vh;
  position: relative;
}
.container .img-hero {
  position: absolute;
  width: 505px;
  height: 330px;
  left: 230px;
  top: 96px;
}

.overlay {
  display: none;
  position: absolute;
  width: 1440px;
  height: 960px;
  left: 0px;
  top: 0px;
  background: #121212;
  opacity: 0.5;
  z-index: 2;
}

.alert {
  display: none;
  position: absolute;
  width: 920px;
  height: 210px;
  left: 260px;
  top: 375px;
  background: #FFFFFF;
  z-index: 3;
}
.alert .rst {
  position: absolute;
  top: 55%;
  left: 50%;
  padding: 16px 40px;
  gap: 10px;
  border: none;
  background: #E4C1AE;
  border-radius: 32px;
}
.alert .rst:hover {
  cursor: pointer;
  background: #c39276;
}
.alert .rst .span-btn {
  width: 48px;
  height: 16px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #121212;
  order: 0;
  flex-grow: 0;
}

.active {
  display: block;
}

.alert-title {
  position: absolute;
  width: 380px;
  height: 50px;
  left: calc(50% - 190px - 175px);
  top: calc(50% - 25px - 24px);
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
}

.alert-description {
  position: absolute;
  width: 380px;
  height: 24px;
  left: calc(50% - 190px - 175px);
  top: calc(50% - 12px + 37px);
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: #121212;
}
.alert-description .numOfClick {
  font-weight: bold;
}

.content {
  position: absolute;
  width: 380px;
  height: 250px;
  left: 830px;
  top: 136px;
  margin-left: 95px;
}
.content .content-title {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
}
.content .content-description {
  padding-top: 24px;
  padding-bottom: 30px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: #121212;
}
.content .content-btn {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 16px 40px;
  gap: 10px;
  border: none;
  background: #E4C1AE;
  border-radius: 32px;
}
.content .content-btn:hover {
  cursor: pointer;
}
.content .content-btn .span-btn {
  width: 48px;
  height: 16px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #fff;
  flex: none;
  order: 0;
  flex-grow: 0;
}

@media (max-width: 1430px) {
  .img-hero {
    position: absolute;
    left: 46%;
    top: 30%;
    transform: translate(-50%, -50%);
  }

  .content {
    position: absolute;
    left: 50%;
    top: 65%;
    transform: translate(-50%, -50%);
    margin-left: 0;
  }
  .content .content-title {
    text-align: center;
  }
  .content .content-description {
    text-align: center;
  }
  .content .content-btn {
    display: block;
    margin: 0 auto;
  }
}